import config from "@/config/auth_config.js"
import { getDefineToken } from './auth'
let Debug=false
export default function initApp() {
	/**
	 * 
	 */
	let list = ["navigateTo", "redirectTo", "reLaunch", "switchTab"];
	list.forEach(item => { //,uni.navigateTo,uni.redirectTo,uni.reLaunch,uni.switchTab4
		uni.addInterceptor(item, {
			invoke(e) { // 
				//token
				const token = uni.getStorageSync('token')
				//token
				// let tokenExpired = uni.getStorageSync('token_expired') < Date.now(),
				let tokenExpired = ""
				let userinfo = getDefineToken('userinfo') || ''
				//url"?""?"
				let url = e.url.split('?')[0];
				let notNeed = config.whiteList.includes(url)
				// whiteList
				if (notNeed) {
					return e
				} else {
					//
					if (token == '') {
						// uni.showToast({
						// 	title: '',
						// 	icon: 'none'
						// })
						uni.navigateTo({
							url: config.loginPage
						})
						return false
					} else {
						if (userinfo.is_auth != 1&&url!=config.authPage) {
							uni.showModal({
								title: '',
								content: '',
								confirmText: '',
								success(res) {
									if (res.confirm) {
										uni.navigateTo({
											url: config.authPage
										})
											
									}
								}
							});
							return false
						}
						return e
					}
				}

			},
			fail(err) { //  
				console.log(err);
				if (Debug) {
					console.log(err);
					uni.showModal({
						content: JSON.stringify(err),
						showCancel: false
					});
				}
			},
			complete(e) {}
		})
	})
	//uniCloud
}
