// import Vue from 'vue'
import axios from 'axios'
import {
	getToken
} from '@/utils/auth.js'

// create an axios instance
const service = axios.create({
	baseURL: 'https://wldt.nxcsoft.top/api/v1', // url = base url + request url
	//withCredentials: true, // send cookies when cross-domain requests withCredentialscross
	timeout: 6000, // request timeout
	crossDomain: true
})

// request,
service.interceptors.request.use(config => {
		//
		if (getToken()) {
			// token
			config.headers["access-user-token"] = getToken();
		}
		config.headers["app-type"] = 'ios';
		return config;
	},
	error => {
		console.log(error); // for debug
		return Promise.reject(error);
	}
);

//
service.interceptors.response.use(res => {
	res = res.data
	if (res.status !== 200) {
		// 
		if(res.status==203){
			uni.redirectTo({
				url:'/pages/login/login'
			})
			return Promise.reject(res)
		}
		return uni.showToast({
			title: res.message,
			icon: 'none'
		}) && Promise.reject(res.message);
	}
	console.log(res); // for debug
	return res.data
}, error => {
	if (error.response.status) {
		switch (error.response.status) {
			case 401:
				break;
			default:
				break;
		}
	}
	return Promise.reject(error)
})

export default service
